.color-select {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.4);
  z-index: 30;
}
.color-select .select-container {
  position: absolute;
  z-index: 11;
  left: 0;
  bottom: 0;
  width: 100%;
  color: #333;
  background: #fff;
}
.color-select .select-title {
  text-align: center;
  height: 1.066667rem;
  line-height: 1.066667rem;
  color: #666;
  font-size: 0.426667rem;
  border-bottom: 1px solid #f0f0f0;
  padding: 0 0.3rem;
}
.color-select .select-title .close-select {
  float: left;
  font-size: 0.373333rem;
}
.color-select .select-title .sure-select {
  float: right;
  font-size: 0.373333rem;
}
.color-select .value-container {
  padding: 0 0.4rem 0.4rem 0.4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  font-size: 0;
}
.color-select .value-container span {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.8rem;
  height: 1.066667rem;
  font-size: 0.4rem;
  color: #666;
  border-radius: 4px;
  border: 1px solid #f0f0f0;
  margin-top: 0.4rem;
}
.color-select .value-container .active {
  color: #2a8cff;
  border: 1px solid #2a8cff;
  background-color: #ecf4fd;
}
